
.modal {
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.75);
  display: none;
 /* justify-content: center;
  align-items: center;
*/}






.modal-container {
  position: relative;
  padding: 1rem;
  min-width: 320px;
  max-width: 480px;
  min-height: 200px;
  max-height: 600px;
  overflow-y: auto;
  background-color: #854D0D;
}

.modal-close {
  z-index: 100;
  position: absolute;
  top: 1rem;
  right: 1rem;
  text-decoration-color: white;
}

.modal.is-open {
  display: flex;
}



.container-info {
  position: relative;
  width: 100%;
  min-height: 50vh;
  display: flex;
/*  justify-content: center;
  align-items: center;*/
  flex-wrap: wrap;


  background: #222;
}

.card-info {
  position: relative;
  width: 100%;
  height: 100%;
  background: #2e2e2e;
  display: flex;

}



.card-info .content {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  text-align: center;
  background: #2e2e2e;
  gap: 20px;
  overflow: hidden;
  z-index: 10;
}

.card-info .content-img {
  position: relative;
  max-width: 100px;
  max-height: 100px;
  border-radius: 50%;
  top: 10px ;
  overflow: hidden;
  z-index: 10;
}

.card-info .content-img img{
z-index:15;
position: relative;
transform: scaleX(-1);
right: 95%;
top: -50px;
width: 200%;
height: 200%;
object-fit: cover;
}

.card-info .name{
z-index:15;
position: relative;
top: -10px;
text-align: left;
object-fit: cover;
}

.card-info .mint{
z-index:15;
position: absolute;
top: 0px;
left: 40%;
text-align: left;
object-fit: cover;
}

.card-info .salud{
z-index:15;
position: absolute;
top: 22px;
left: 42%;
text-align:left;
object-fit: cover;
display: flex;
}

.card-info .salud .svg{
width: 1.2rem;
height: 1.2rem;
fill:white;
}

.card-info .resistencia{
z-index:15;
position: absolute;
top: 44px;
left: 45%;
text-align:left;
object-fit: cover;
display: flex;
}

.card-info .resistencia .svg{
width: 1.5rem;
height: 1.5rem;
fill:white;
}

.card-info .temperamento{
z-index:15;
position: absolute;
top: 68px;
left: 45%;
text-align:left;
object-fit: cover;
display: flex;
}

.card-info .temperamento .svg{
width: 1.4 rem;
height: 1.4rem;
fill:white;
}



.card-info .morfologia{
z-index:15;
position: absolute;
top: 88px;
left: 38%;
text-align:left;
object-fit: cover;
display: flex;
}

.card-info .morfologia .svg{
width: 1.5rem;
height: 1.5rem;
fill:white;
}


.card-info .sexo{
z-index:15;
position: absolute;
top: 0px;
left: 1px;
display: flex;
}


.card-info .content .icon {
  color: var(--color);
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
  background: #2e2e2e;
  box-shadow: 0 0 0 4px #2e2e2e,
    0 0 0 6px var(--color);
  transition: 0.5s ease-in-out;
}

.card-info:hover .content .icon {
  background: var(--color);
  color: #2e2e2e;
  box-shadow: 0 0 0 4px #2e2e2e,
    0 0 0 300px var(--color);
}

.card-info .content .text h3 {
  color: #fff;
  font-size: 1.5rem;
  font-weight: 500;
  transition: 0.5s ease-in-out;
}

.card-info:hover .content .text h3 {
  color: #2e2e2e;
}

.card-info .content .text p {
  color: #999;
  transition: 0.5s ease-in-out;
}



.card-info .content .text a {
  position: relative;
  display: inline-block;
  background: var(--color);
  color: #2e2e2e;
  text-decoration: none;
  font-weight: 500;
  margin-block-start: 10px;
  padding: 8px 15px;
}

.card-info:hover .content .text a {
  background: #2e2e2e;
  color: var(--color);
}






